import { useCallback, useEffect, useState } from "react";
import style from "../../FinanceManager/FinanceManager.module.css";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../../Utils/Functions/nextPage";
import { customDebounce } from "../../../Utils/Functions/debounce";
import UserMemberContainerWrapper from "../../../Ui/UserMember/UserMemberContainerWrapper";
import UserMemberContentWrapper from "../../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../../Ui/Border/BorderBottom";
import QvipleLoader from "../../../Loader/QvipleLoader";
import Loading from "../../../Loader/Loading";
import EmptyMessage from "../../../Utils/EmptyMessage/EmptyMessage";
import { useLmsModeratorAllStaffList } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";
import { useTranslation } from "react-i18next";
import { assestsNavbarUrl } from "../../../Services/UrlConfig/AssestsBaseUrl";
import LmsRecommendationAuthorityStaffCard from "./LmsRecommendationAuthorityStaffCard";
import LmsRecommendationAuthorityAddStaff from "./LmsRecommendationAuthorityAddStaff";
import CreateButton from "../../../Ui/Button/CreateButton";

const LmsRecommendationAuthorityStaff = ({ modId, instituteId }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);
  const {
    lmsModeratorAllStaffList,
    lmsModeratorAllStaffListLoading,
    lmsModeratorAllStaffListRefetch,
  } = useLmsModeratorAllStaffList({
    data: {
      mid: modId,
      page: page,
      limit: 10,
      search: search,
      flow: "Recommend_Section",
    },
    skip: !modId,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (lmsModeratorAllStaffList?.list?.length) {
      let obj = nextPage(lmsModeratorAllStaffList?.list?.length);
      setState(() => obj);
    }
  }, [lmsModeratorAllStaffList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    lmsModeratorAllStaffListRefetch();
  }, [lmsModeratorAllStaffListRefetch]);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <UserMemberContainerWrapper>
      <UserMemberContentWrapper>
        <div className={style.fm_header_container}>
          <h6>{t("all_staffs")}</h6>
          <div className={style.fm_search}>
            <section className={style.fm_search_container}>
              <div className={style.fm_search_container_inner}>
                <img
                  className={style.fm_search_icon}
                  alt="search icon"
                  src={`${assestsNavbarUrl}/navbar-search.svg`}
                />
                <input
                  type="text"
                  placeholder={t("search")}
                  onChange={onSearchEvent}
                />
              </div>
            </section>
            <CreateButton
              label={t("add_new_staff")}
              customStyle={{
                margin: "0",
                padding: "0.4rem 1rem",
              }}
              onAction={onToggle}
            />
          </div>
        </div>
      </UserMemberContentWrapper>
      <BorderBottom />
      <UserMemberContentWrapper>
        {lmsModeratorAllStaffList?.list?.map((staff, index) =>
          lmsModeratorAllStaffList?.list?.length === index + 1 ? (
            <div key={staff?._id} ref={ref}>
              <LmsRecommendationAuthorityStaffCard
                staff={staff}
                onRefetch={onRefetch}
                flow="Recommend_Section"
                modId={modId}
              />
            </div>
          ) : (
            <LmsRecommendationAuthorityStaffCard
              staff={staff}
              key={staff?._id}
              onRefetch={onRefetch}
              flow="Recommend_Section"
              modId={modId}
            />
          )
        )}
        {lmsModeratorAllStaffListLoading ? (
          search ? (
            <QvipleLoader />
          ) : (
            <Loading />
          )
        ) : search ? (
          lmsModeratorAllStaffList?.list?.length === 0 && (
            <EmptyMessage title="No staff leave history found related this search." />
          )
        ) : (
          lmsModeratorAllStaffList?.list?.length === 0 && (
            <EmptyMessage title="No staff leave history." />
          )
        )}
        {add && (
          <LmsRecommendationAuthorityAddStaff
            instituteId={instituteId}
            modId={modId}
            onRefetch={onRefetch}
            onClose={onToggle}
          />
        )}
      </UserMemberContentWrapper>
    </UserMemberContainerWrapper>
  );
};

export default LmsRecommendationAuthorityStaff;
