import { useState } from "react";
import InstituteStaff from "../../InstituteStaff/InstituteStaff";
import QvipleLoader from "../../../Loader/QvipleLoader";
import { useLmsModeratorAddStaff } from "../../../Hooks/ApiHooks/LmsManager/lms-manager-api";

const LmsRecommendationAuthorityAddStaff = ({
  instituteId,
  modId,
  onRefetch,
  onClose,
}) => {
  const [staffListId, setStaffListId] = useState([]);
  const [staffListId1, setStaffListId1] = useState([]);
  const [disabled, setDisabled] = useState(false);
  const [lmsModeratorAddStaff] = useLmsModeratorAddStaff();

  const onSelect = (val) => {
    if (staffListId?.includes(val?._id))
      setStaffListId((prev) => prev?.filter((id) => id !== val?._id));
    else setStaffListId((prev) => [...prev, val?._id]);
  };
  const onAddStaff = () => {
    if (modId && staffListId?.length > 0) {
      setDisabled((pre) => !pre);
      lmsModeratorAddStaff({
        mid: modId,
        addStaff: {
          flow: "Recommend_Section",
          staff_arr: staffListId,
        },
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  return (
    <>
      <InstituteStaff
        title="add_staff"
        onClose={onClose}
        instituteId={instituteId}
        isMultiSelect
        onMultiSelect={onSelect}
        selectList={[...staffListId, ...staffListId1]}
        onDone={onAddStaff}
      />
      {disabled && <QvipleLoader />}
    </>
  );
};

export default LmsRecommendationAuthorityAddStaff;
